.wrap #page2 .page_box .page_inner .my_skill > li .circle .right .progress,
.wrap #page2 .page_box .page_inner .my_skill > li .circle .right1 .progress,
.wrap #page2 .page_box .page_inner .my_skill > li .circle .right2 .progress,
.wrap #page2 .page_box .page_inner .my_skill > li .circle .right3 .progress {
  animation-delay: 3s;
}
@keyframes right {
  100% {
    transform: rotate(162deg);
  }
}
@keyframes right1 {
  100% {
    transform: rotate(162deg);
  }
}
@keyframes right2 {
  100% {
    transform: rotate(108deg);
  }
}
@keyframes right3 {
  100% {
    transform: rotate(72deg);
  }
}
